___CSS_LOADER_EXPORT___.push([module.id, `*{
    margin: 0;
    padding: 0;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

section.erro404{
    height: 600px;
}

section.erro404 h1{
    text-align: center;
    margin: 50px 0px;
    font-size: 16em;
}

section.erro404 p{
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.8em;
}

section{
    height: 65vh;
    box-shadow: 1px 5px 10px black;
    margin-top: 35px;
    width: 35vw;
}

h1{
    text-align: center;
    font-size: 2rem;
    margin-top: 40px;
    margin-bottom: 17px;
    color: #1d8ab9;
}

p{
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.5em;
    color: #22B2F0;
    margin: 10px 0;
}

label{
    display: block;
    text-align: left;
    font-size: 1.6em;
    margin-top: 18px;
    margin-left: 10px;
    color: #22B2F0;
}

section.login{
    width: 7vh;
}

input, .botao-contato{
    height: 37px;
    width: 33vw;
    padding-left: 8px;
    box-sizing: border-box;
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: 5px;
    font-size: 1.1em;
    border-radius: 3%;
    font-family: Arial, Helvetica, sans-serif;
}

input:hover, select:hover, option:hover{
    border: 1px solid #2fc1ff;
}

.botao{
    background-color: #1d8ab9;
    color: white;
    border: 1px solid #1d8ab9;
    margin-top: 20px;
    cursor: pointer;
}

.botao:hover{
    background-color: #2fc0ff;
    border: 1px solid #2fc1ff;
}

.link-register{
    margin-top: 20px;
    text-decoration: none;
    padding-left: 5px;
    color: #2fc0ff;
}

.link-register:hover{
    color: #1f8ab9;
    text-decoration: underline;
}

.mensagem-erro, .mensagens-erro{
    color: red;
    list-style: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.1em;
    text-align: center;
    margin: 20px 0;
}

.mensagem-erro li, .mensagem-success li{
    list-style: none;
}

.mensagem-success{
    color: green;
    list-style: none;
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1.1em;
    text-align: center;
    margin: 22px 0;
}

.nav{
    width: 100vw;
    font-size: 1.5em;
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-family: Arial, Helvetica, sans-serif;
}

.nav ul{
    display: flex;
}

.nav li{
    list-style: none;
    margin: 0 20px;
    padding: 20px 0;
}

.nav a{
    text-decoration: none;
    color: #22B2F0;
}

.nav-log{
    padding: 0 25px;
}

section.contato{
    height: 83vh;
    width: 35vw;
}

.contato input, .botao-contato{
    width: 32vw;
}

.tipo{
    display: block;
    height: 5vh;
    width: 15vw;
    padding-left: 5px;
    margin-top: 10px;
    margin-left: 10px;
    margin-bottom: 10px;
    font-size: 1.1em;
    border-radius: 2%;
    font-family: Arial, Helvetica, sans-serif;
    cursor: pointer;
}

section.home{
    width: 100%;
    height: 100%;
    box-shadow: none;
   
}

.home table{
    display: flex;
    justify-content: center;
    align-items: center;
    border-spacing: 15px;
}

.home td{
    padding: 13px;
    text-align: center;
    font-family: Arial, Helvetica, sans-serif;
}

.contatos td{
    font-size: 1.2em;
}

.editar{
    color: #00DD55;
    text-decoration: none;
}

.editar:hover{
    color: #00A43F;
    text-decoration: underline;
}

.excluir{
    color: #DD190B;
    text-decoration: none;
}

.excluir:hover{
    color: #A41308;
    text-decoration: underline;
}`, "",{"version":3,"sources":["webpack://./js/Node/front-end/assets/css/style.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;AACd;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,sBAAsB;AAC1B;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,kBAAkB;IAClB,yCAAyC;IACzC,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,8BAA8B;IAC9B,gBAAgB;IAChB,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,yCAAyC;IACzC,gBAAgB;IAChB,cAAc;IACd,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,gBAAgB;IAChB,gBAAgB;IAChB,gBAAgB;IAChB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,iBAAiB;IACjB,sBAAsB;IACtB,gBAAgB;IAChB,iBAAiB;IACjB,kBAAkB;IAClB,gBAAgB;IAChB,iBAAiB;IACjB,yCAAyC;AAC7C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,yBAAyB;IACzB,gBAAgB;IAChB,eAAe;AACnB;;AAEA;IACI,yBAAyB;IACzB,yBAAyB;AAC7B;;AAEA;IACI,gBAAgB;IAChB,qBAAqB;IACrB,iBAAiB;IACjB,cAAc;AAClB;;AAEA;IACI,cAAc;IACd,0BAA0B;AAC9B;;AAEA;IACI,UAAU;IACV,gBAAgB;IAChB,yCAAyC;IACzC,gBAAgB;IAChB,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,yCAAyC;IACzC,gBAAgB;IAChB,kBAAkB;IAClB,cAAc;AAClB;;AAEA;IACI,YAAY;IACZ,gBAAgB;IAChB,aAAa;IACb,8BAA8B;IAC9B,mBAAmB;IACnB,yCAAyC;AAC7C;;AAEA;IACI,aAAa;AACjB;;AAEA;IACI,gBAAgB;IAChB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,qBAAqB;IACrB,cAAc;AAClB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,cAAc;IACd,WAAW;IACX,WAAW;IACX,iBAAiB;IACjB,gBAAgB;IAChB,iBAAiB;IACjB,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;IACjB,yCAAyC;IACzC,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,gBAAgB;;AAEpB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,kBAAkB;IAClB,yCAAyC;AAC7C;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,0BAA0B;AAC9B;;AAEA;IACI,cAAc;IACd,qBAAqB;AACzB;;AAEA;IACI,cAAc;IACd,0BAA0B;AAC9B","sourcesContent":["*{\r\n    margin: 0;\r\n    padding: 0;\r\n}\r\n\r\nbody{\r\n    display: flex;\r\n    jus